<template>
  <div id="app">
    <div class="align-left loading">
      <img src="./assets/loading.gif" alt=""/>
    </div>
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item active">
          <router-link :to="{name: 'home'}" class="nav-link">Home</router-link>
        </li>
        <li class="nav-item">
          <router-link :to="{name: 's3Repos'}" class="nav-link">S3 Repo</router-link>
        </li>
        <li class="nav-item">
          <router-link :to="{name: 'users'}" class="nav-link">User Mgmt</router-link>
        </li>
        <!--        <li class="nav-item">-->
        <!--          <router-link :to="{name: 'post', params: {slug: 'any-post'}}" class="nav-link">Post</router-link>-->
        <!--        </li>-->
      </ul>
      <form class="form-inline my-2 my-lg-0" id="logout" v-show="isAuthenticated">
        <button class="btn btn-outline-success my-2 my-sm-0" @click="logout()">Logout</button>
      </form>
    </nav>
    <router-view></router-view>
  </div>
</template>

<script>

import {getSCacheEx, initSCacheEx} from './assets/cache.js'
import $ from "jquery";

export default {
  name: 'app',
  components: {},
  data() {
    return {
      isAuthenticated: getSCacheEx('session', 'isAuthenticated') === 'true',
    };
  },
  methods: {
    logout() {
      $('.loading').show();
      let apiUrl = document.location.protocol + '//' + document.location.hostname;
      if (document.location.hostname === 'repo.devops.doohee323.local') {
        apiUrl += ':8080/api/';
      } else {
        apiUrl += '/api/';
      }
      alert(1);
      let vData = {
        token: getSCacheEx('session', 'token'),
      }
      $.ajax({
        method: "POST",
        url: apiUrl + 'auth/logout',
        data: JSON.stringify(vData),
      }).done(function (e) {
        console.log(e);
        $('.loading').hide();
        initSCacheEx('session', 'isAuthenticated');
        initSCacheEx('session', 'token');
        initSCacheEx('session', 'user');
        initSCacheEx('session', 'admin');
      });
    },
  },
  mounted() {
    debugger;
    if (getSCacheEx('session', 'isAuthenticated') !== 'true') {
      if (this.$route.name !== 'Login') {
        this.$router.push('/login');
      }
    }
  },
}
</script>
