<script>

// import confirmationModal from 'confirmation-modal.vue';
import $ from 'jquery';
import dataTable from '../common/data-table.vue';
import {getSCacheEx} from "@/assets/cache";

export default {
  name: 'switch-updatedDt-entries',
  components: {
    // confirmationModal,
    dataTable,
  },
  props: [
    'title',
    'path',
  ],
  data() {
    return {
      writable: getSCacheEx('session', 'admin') === 'true',
      s3repo: 'unity-repo.topzone.com',
      s3repos: ['unity-repo.topzone.com'],
      s3RepoEntries: [],
      actionButtons: this.writable ? [
        {
          label: 'Delete',
          icon: 'minus-square',
          context: 'danger',
          event: 'delete',
        },
      ] : null,
      messages: {
        confirmDeletion: 'Confirm Item Deletion',
        zeroRecords: 'No matching items found',
      },
      selectedS3RepoEntries: [],
      repoTableColumnDefs: [
        {"width": "10%", "targets": 0},
        {"width": "10%", "targets": 1},
        {"width": "10%", "targets": 2},
        {"width": "40%", "targets": 3},
        {"width": "10%", "targets": 4},
        {"width": "20%", "targets": 5},
      ],
    }
  },
  methods: {
    cellsData(e) {
      // let link = `<a href="${e.filePath}" onclick="window.open('${e.filePath}', '_blank')" class="btn btn-primary btn-sm">
      //                           <span class="fas fa-download"></span>Download
      //                       </a>`;
      let link2 = `<a href="#" class="btn btn-primary btn-sm" @click="delete">
                                <span class="fas fa-trash"></span>Delete
                            </a>`;
      return [
        {'data-order': e.project, 'html': e.project},
        {'data-order': e.updatedDt, 'html': e.updatedDt},
        {
          'data-order': e.fileType,
          'html': `<span class="fa-brands fa-${e.fileType}"></span>`
        },
        {'data-order': e.filePath, 'html': `<a href="${e.filePath}" target="_blank">${e.filePath}</a>`},
        e.size,
        this.writable ? link2 : '',
      ];
    },
    clickRow(e, $currentRow) {
      const target = $(e.target);
      if (target.children().hasClass('fa-download')) {
        debugger;
        // window.open(this.s3RepoEntries[$currentRow].filePath, '', 'width=300, height=300');
      } else if (target.children().hasClass('fa-trash')) {
        this.deleteItem(this.s3RepoEntries[$currentRow].filePath);
      }
    },
    mountedData() {
      // debugger;
    },
    requestData(cb) {
      let $this = this;
      $('.loading').show();
      let apiUrl = document.location.protocol + '//' + document.location.hostname;
      // apiUrl = 'https://repo.devops.topzone.com'
      if (document.location.hostname === 'repo.devops.doohee323.local') {
        apiUrl += ':8080/api/';
      } else {
        apiUrl += '/api/';
      }
      const url = apiUrl + 's3repo?s3repo=' + this.s3repo;
      $.get(url).then(function (res) {
        res = JSON.parse(res);
        $this.S3 = res;
        cb(res);
        $('.loading').hide();
      });
    },
    getItems($this) {
      this.requestData((e) => {
        for (let i in e) {
          if (e[i].filePath.endsWith('apk')) {
            e[i]['fileType'] = 'android';
          } else if (e[i].filePath.endsWith('zip')) {
            e[i]['fileType'] = 'apple';
          }
        }
        $this.s3RepoEntries = e;
        $this.$refs.s3RepoDataTable.mountedPromise.then(
            () => {
              $this.$refs.s3RepoDataTable.load();
            }
        );
      });
    },
    deleteItem() {
      let $this = this;
      $('.loading').show();
      let apiUrl = document.location.protocol + '//' + document.location.hostname;
      if (document.location.hostname === 'repo.devops.doohee323.local') {
        apiUrl += ':8080/';
      } else {
        apiUrl += '/';
      }
      const url = apiUrl + 's3repo?s3repo=' + this.s3repo;
      $.post(url).then(function () {
        $this.getItems($this);
        $('.loading').hide();
      });
    },
  },
  mounted() {
    this.s3RepoEntries = [
      {
        project: 'tz-unity-devops',
        filePath: 'tz-unity-devops_mac_2021.3.29f1_Android_20240217_014556.apk',
        updatedDt: '20240101',
        fileType: 'apk',
        size: 100
      },
      {
        project: 'sl_flutter_devops',
        filePath: 'sl_flutter_devops_android_flutter__20240229_140805.zip',
        updatedDt: '20240102',
        fileType: 'apk2',
        size: 101
      },
    ];
    this.getItems(this);
  },
};

</script>