export function setCache(cacheKey, cacheData, expires) {
    setCacheEx('default', cacheKey, cacheData, expires);
}

export function getCache(cacheKey) {
    return getCacheEx('default', cacheKey);
}

export function initCache(cacheKey) {
    initCacheEx('default', cacheKey);
}

export function getCacheKey(group, cacheKey) {
    cacheKey = cacheKey.replace(/&/g, '').replace(/\?/g, '').replace(/,/g, '');
    return group + '-' + cacheKey;
}

export function setCacheEx(group, cacheKey, cacheData, expires) {
    let newCacheKey = getCacheKey(group, cacheKey);
    if (expires === undefined || expires === 'null') {
        expires = 60*60*24;
    }
    let date = new Date();
    let schedule = Math.round((date.setSeconds(date.getSeconds() + expires)) / 1000);
    try {
        localStorage.setItem(newCacheKey, cacheData);
        localStorage.setItem(newCacheKey + '_time', schedule);
    } catch (e) {
        initCacheEx(group, cacheKey);
        localStorage.setItem(newCacheKey, cacheData);
        localStorage.setItem(newCacheKey + '_time', schedule);
    }
}
export function getCacheEx(group, cacheKey) {
    try {
        let newCacheKey = getCacheKey(group, cacheKey);
        let date = new Date();
        let current = Math.round(date / 1000);
        let stored_time = localStorage.getItem(newCacheKey + '_time');
        if (stored_time === undefined || stored_time === null) {
            stored_time = 0;
        }
        if (stored_time < current) {
            initCacheEx(group, cacheKey);
            return '';
        } else {
            return localStorage.getItem(newCacheKey);
        }
    } catch (e) {
        console.log(e);
    }
}

export function initCacheEx(group, cacheKey) {
    cacheKey = getCacheKey(group, cacheKey);
    localStorage.setItem(cacheKey, null);
    localStorage.setItem(cacheKey + '_time', null);
}

export function setSCacheEx(group, cacheKey, cacheData, expires) {
    let newCacheKey = getCacheKey(group, cacheKey);
    if (expires === undefined || expires === null) {
        expires = 10000;
    }
    let date = new Date();
    let schedule = Math.round((date.setSeconds(date.getSeconds() + expires)) / 1000);
    try {
        sessionStorage.setItem(newCacheKey, cacheData);
        sessionStorage.setItem(newCacheKey + '_time', schedule);
    } catch (e) {
        initSCacheEx(group, cacheKey);
        sessionStorage.setItem(newCacheKey, cacheData);
        sessionStorage.setItem(newCacheKey + '_time', schedule);
    }
}
export function getSCacheEx(group, cacheKey) {
    try {
        let newCacheKey = getCacheKey(group, cacheKey);
        let date = new Date();
        let current = Math.round(date / 1000);
        let stored_time = sessionStorage.getItem(newCacheKey + '_time');
        if (stored_time === undefined || stored_time === null) {
            stored_time = 0;
        }
        if (stored_time < current) {
            initSCacheEx(group, cacheKey);
            return '';
        } else {
            return sessionStorage.getItem(newCacheKey);
        }
    } catch (e) {
        console.log(e);
    }
}

export function initSCacheEx(group, cacheKey) {
    cacheKey = getCacheKey(group, cacheKey);
    sessionStorage.setItem(cacheKey, null);
    sessionStorage.setItem(cacheKey + '_time', null);
}
