const
    $ = require('jquery');

export let $forms;

window.ParsleyConfig = {
    classHandler: function(f) { return f.$element.closest('.error-handler'); },
    errorClass: "is-invalid",
    errorsContainer: function(f) { return f.$element.closest('.error-handler'); },
    errorsWrapper: "<ul class='list-unstyled help-block is-invalid col-lg-12'></ul>",
    excluded: "input[type=button], input[type=submit], input[type=reset], input[type=hidden], [disabled], :hidden:not(.modal *)",
    trigger: "change keyup select"
};
require('parsleyjs');
require('./parsley-validators');

export function showEnabled(enabler) {
    const enablees = $('[data-enabler=' + enabler.id + ']');
    if (enabler.checked) { enablees.show(); } else { enablees.hide(); }
    const $form = $(enabler).closest('form');
    if ($form.length > 0) { $form.parsley().validate(); }
}

let $enablers = $();
function resetEnablers() {
    $enablers.off('change');
    $enablers = $('input[type=checkbox].enabler, input[type=radio].enabler');
    $enablers.change(function(e) { showEnabled(e.target); });
    $forms = $enablers.each(function(i, s) { showEnabled(s); }).closest('form');
}
resetEnablers();

export function showDisabled(disabler) {
    const disablees = $('[data-disabler=' + disabler.id + ']');
    if (disabler.checked) { disablees.hide(); } else { disablees.show(); }
    const $form = $(disabler).closest('form');
    if ($form.length > 0) { $form.parsley().validate(); }
}

let $disablers = $();
export function resetDisablers() {
    $disablers.off('change');
    $disablers = $('input[type=checkbox].disabler, input[type=radio].disabler');
    $disablers.change(function(e) { showDisabled(e.target); });
    $forms = $disablers.each(function(i, s) { showDisabled(s); }).closest('form');
}
resetDisablers();

export function resetEnableDisable() {
    resetEnablers();
    resetDisablers();
}

export function initializeParsley($forms) {
    $forms.each((_, form) => {
        const $form = $(form);
        $form.off('submit.Parsley');
        const parsley = $form.parsley();
        parsley.on('field:validated', function(e) {
            console.log(e);
            $form.find(':submit').attr('disabled', !parsley.isValid());
        });
        if (!$form.hasClass('wait')){
            parsley.validate();
        } else {
            $form.find(':submit').attr('disabled', true);
        }
        form.addEventListener('submit', e => {
            e.preventDefault();
        });
    });
}

initializeParsley($('form[data-parsley-validate]'));

export function destroyParsley($forms) {
    $forms.each((_, form) => {
        $(form).parsley().destroy();
    });
}

export function resetParsley($forms) {
    destroyParsley($forms);
    initializeParsley($forms);
}

export function showAlert(isShow, status, _show_time) {
    const ajax_alert = $(".ajax-alert");
    const ajax_msg = $("#ajax_msg");
    if (isShow) {
        ajax_alert.removeClass('d-none').css('display', '');
        let show_time = 3000;
        if (!status) {
            ajax_alert.addClass('alert-success');
        } else {
            show_time = 5000;
            ajax_alert.addClass('alert-' + status);
        }
        if (_show_time) {
            show_time = _show_time;
        }
        setTimeout(function(){
            ajax_msg.text('');
            ajax_alert.addClass('d-none').removeClass('alert-danger alert-warning alert-info');
        }, show_time);
    } else {
        ajax_msg.text('');
        ajax_alert.addClass('d-none');
    }
}

export function initializeModal() {
    $('.modal[role=dialog] button.once').on('click', function (e) {
        setTimeout(function () {
            $(e.target).prop('disabled', true);
        }, 1);
    });

    $('.ajax-alert').on('close.bs.alert', function (e) {
        e.preventDefault();
        showAlert(false);
    });

    $('input, select').on('keyup keydown keypress change paste', function () {
        showAlert(false);
    });
}