import HomePage from "@/components/Home-page.vue";
import AboutPage from "@/components/About-page.vue";
import PostPage from "@/components/Post-page.vue";
import NotfoundPage from "@/components/Notfound-page.vue";
import LoginPage from "@/components/Login-page.vue";
import S3Repos from "@/components/s3Repo/S3Repos-page.vue";
import Users from "@/components/user/Users-page.vue";
import UserAdd from "@/components/user/Add-page.vue";
import UserEdit from "@/components/user/Edit-page.vue";

export default [
    {
        path: '/',
        component: HomePage,
        name: 'home',
    },
    {
        path: '/about',
        component: AboutPage,
        name: 'about',
    },
    {
        path: '/users',
        component: Users,
        name: 'users',
    },
    {
        path: '/user/add',
        component: UserAdd,
        name: 'userAdd',
    },
    {
        path: '/user/edit',
        component: UserEdit,
        name: 'userEdit',
    },
    {
        path: '/post/:slug',
        component: PostPage,
        name: 'post',
        props: true,
    },
    {
        path: '/s3Repos',
        component: S3Repos,
        name: 's3Repos',
    },
    {
        path: '/login',
        component: LoginPage,
        name: 'login',
    },
    {
        path: '*', beforeEnter: (to, from, next) => {
            // next('/404')
            next('/')
        }
    },
    {
        path: '/404',
        name: '404',
        component: NotfoundPage
    },
]